.gpt3__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.gpt3__features-heading{
    flex:1;
    margin-right: 2rem;
}

.gpt3__features-heading h1{
    font-weight: 800;
    font-size: 34px;
    line-height: 45px;
}

.gpt3__features-heading p {
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: #FF8A71;
    margin-top: 2rem;
}

.gpt3__features-container {
    flex:1.5;
}

@media (max-width:990px){
    .gpt3__features {
        flex-direction: column;
    }

    .gpt3__features-heading {
        margin: 0 0 2rem 0;
    }
}

@media (max-width:550px){
    .gpt3__features-heading h1{
        font-size: 28px;
        line-height: 38px;
    }
}


