.gpt3__cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding:2rem;
    margin: 4rem;
    color: #fff;
    border-radius: 1rem;
    background: var(--gradient-bar);
}

.gpt3__cta-content p {
    font-weight: 500;
    font-size: 12px;
    line-height: 30px;
    color: #0E0E0E;
}

.gpt3__cta-content h3 {
    font-weight: 800;
    font-size: 24px;
    line-height: 45px;
    color: #000000;
}

.gpt3__cta-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.gpt3__cta-btn button {
    min-width: 134px;
    font-weight: bold;
    font-size: 18px;
    line-height: 30px;
    color: #FFFFFF;
    background: #000000;
    border-radius: 40px;
    padding: 0.5rem 1rem;
}

@media (max-width:650px) {
    .gpt3__cta{
        flex-direction: column;
    }

    .gpt3__cta-btn {
        margin: 2rem 0 0;
    }
}

@media (max-width:550px) {
    .gpt3__cta{
        margin: 4rem 2rem;
    }

    .gpt3__cta-content {
        font-size: 18px;
        line-height: 32px;
    }

    .gpt3__cta-btn button {
        font-size: 14px;
        line-height: 28px;
    }
}