.gpt3__blog-article {
    width:100%;
    height:100%;
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}

.gpt3__blog-article-img {
    width:100%;
    height:100%;
    background: var(--color-footer);
}

.gpt3__blog-article-img img {
    width:100%;
    height:100%;
}

.gpt3__blog-article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 1rem 1.5rem;
    height: 100%;
}

.gpt3__blog-article-content time {
    font-weight: bold;
    font-size: 11.649px;
    line-height: 35px;
    color: #FFFFFF;
}

.gpt3__blog-article-content h3 {
    font-weight: 800;
    font-size: 25.11px;
    line-height: 30px;
    color: #FFFFFF;
    margin-bottom: 5rem;
}

.gpt3__blog-article-content p {
    font-weight: bold;
    font-size: 11.649px;
    line-height: 35px;
    color: #FFFFFF;
    cursor: pointer;
}

@media (max-width: 550px) {
    .gpt3__blog-article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}